import { GitHubRepository } from "@/types/github/GitHub";
import { Dialog, DialogTitle, List, ListItem, ListItemButton, ListItemText, TextField, Box, DialogActions, Button, LinearProgress, DialogContent } from "@mui/material";
import React, { FunctionComponent, useEffect } from "react";
import gitHubApi from "@/api/githubService";
import GithubRepoViewer from "./GithubRepoViewer";
import { useTranslation } from "react-i18next";



interface Props {

	/**
	 * se  true la dialog  aperta
	 */
	isOpen: boolean,

	/**
	 * placeholder della text di ricerca
	 */
	placeholder?: string,

	/** 
	 * chiamata quando si clicca sul btt colose o fuori dalla dialog 
	 * restituisce l'item selezionato o null se si  chiusa senza selezionare nulla
	 */
	onClose: (repo: GitHubRepository | null) => void

}

const GithubReposFinderDialog: FunctionComponent<Partial<Props>> = ({
	isOpen,
	placeholder,
	onClose,
}) => {

	// HOOKs
	const { t } = useTranslation()
	const [filterText, setFilterText] = React.useState('')
	const [items, setItems] = React.useState<GitHubRepository[]>([])
	const [loading, setLoading] = React.useState(false)

	useEffect(() => {
		if (!isOpen) return

		const searchRepositories = async () => {
			if (filterText.length < 3) return
			setLoading(true)
			try {
				const result = await gitHubApi.searchRepositories(filterText, 10)
				setItems(result.items)
			} catch (err) {
				setItems([])
			} finally {
				setLoading(false)
			}
		}

		const debounceTimer = setTimeout(() => {
			searchRepositories()
		}, 800)

		return () => clearTimeout(debounceTimer)
	}, [filterText])


	// HANDLERS
	const handleClose = (reason?: 'backdropClick' | 'escapeKeyDown') => {
		onClose(null)
	}

	const handleItemClick = async (repo: GitHubRepository) => {
		onClose(repo)
	}

	const handleFilterChange = (event: React.ChangeEvent<HTMLInputElement>) => {
		setFilterText(event.target.value)
	}


	// RENDER 

	return (

		<Dialog maxWidth="sm" fullWidth
			open={isOpen}
			onClose={handleClose}
		>

			<DialogTitle>Select GitHub Repository</DialogTitle>

			{loading && <LinearProgress />}

			<Box sx={{ px: 3, pb: 2 }}>
				<TextField autoFocus
					value={filterText ?? ''}
					onChange={handleFilterChange}
					placeholder={placeholder}
				/>
			</Box>

			<DialogContent>
				<List sx={{ maxHeight: 400, overflow: 'auto' }}>
					{loading && (
						<ListItem>
							<ListItemText primary="Searching..." />
						</ListItem>
					)}

					{!loading && items.map((repo) => (
						<ListItem divider key={repo.id} disablePadding>
							<ListItemButton onClick={() => handleItemClick(repo)}>
								<GithubRepoViewer noLink repository={repo} />
							</ListItemButton>
						</ListItem>
					))}
				</List>
			</DialogContent>

			<DialogActions>
				<Button onClick={() => handleClose()}>
					{t("common.cancel")}
				</Button>
			</DialogActions>
		</Dialog>
	)
}

export default GithubReposFinderDialog



